.card {
  max-width: 200px;
  box-sizing: border-box;
  border-radius: 5px;
  border: 1px solid #f3f3f3;
  display: flex;
  flex-direction: column;
  padding-inline: 16px;

  &__image {
    height: 134px;
    width: 160px;
    margin-top: 32px;
    margin-inline: 3px;
  }

  &__title {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: #060b35;
    margin-top: 40px;
  }

  &__code {
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: #616070;
    margin-top: 4px;
  }

  &__reviews {
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
    justify-content: space-between;
    margin-top: 16px;

    &-number {
      font-weight: 400;
      font-size: 10px;
      line-height: 14px;
      color: #060b35;
    }
  }

  &__price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 24px;

    &-text {
      font-weight: 400;
      font-size: 12px;
      line-height: 18px;
      color: #616070;
    }

    &-cost {
      font-weight: 700;
      font-size: 16px;
      line-height: 18px;
      color: #060b35;
    }
  }

  &__link {
    background-color: #00acdc;
    width: 168px;
    height: 40px;
    box-sizing: border-box;
    color: #fff;
    border-radius: 5px;
    border: none;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 14px;
    line-height: 40px;
    margin: 16px auto;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
  }
}

.card:hover {
  transform: scale(120%);

  .card__title {
    color: #34568b;
  }

  .card__link {
    color: #00acdc;
    background-color: #fff;
    border: 1px solid #00acdc;
  }
}
